*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: #f1f5f8;
    color: #222;
    /* align-items: center;
    justify-content: center; */
    /* display: flex; */
}

.booklist{
    width: 90vw;
    max-width: 1170px;
    margin: 3rem auto;
    /* display: grid; */
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    /* align-items: start; */

}
.book img{
    width: 100%;
    object-fit: cover;

}

@media  screen and (max-width:600px){
    .booklist{
        display: grid;
        flex-wrap: nowrap;
    }
    .book{
        width: none;
    }
}

@media  screen and (min-width:600px) and (max-width:760px){
    /* .booklist{
        display: grid;
        flex-wrap: nowrap;
    } */
    .book{
        width: 45%;
    }
}
@media  screen and (min-width:760px){
    /* .booklist{
        display: grid;
        flex-wrap: nowrap;
    } */
    .book{
        width: 30%;
    }
    
}
/* @media screen and (min-width:768px){
    .booklist{
        grid-template-columns: repeat(3, 1fr);
    }
} */

.book{
    background: #fff;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    /* width: 30%; */
    

}
.book h2{
    margin-top: 1rem;
    font-size: 1rem;
    
}
.number{
    position: absolute;
    border-radius: 1rem 0 1rem 0;
    color:white;
    height: 2rem;
    padding: 0.4rem;
    font-size: 1rem;
    margin-top: -2rem;
    margin-left: -2rem;
    background-color: rgb(255, 111, 0);
}
.heading{
    padding-top: 1rem;
    text-align: center;
    font-size: 2.5rem;
}
.titleButton{
    /* border-radius:  solid black; */
    border: none;
    border-radius: 2rem;
    padding-right: 1.4rem;
    padding-left: 1.4rem;
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;
    background-color: rgb(0, 157, 255);
    color: white;

}